<script setup>
import { ref, onMounted } from "vue";
import { useStore } from "vuex";
const store = useStore();
const option_education_level = ref([]);
const option_present_type = ref([]);
const option_topic_type = ref([]);
const color_set = ref([]);
const db_test = ref([]);
const summary = ref([]);
onMounted(() => {
  option_education_level.value = store.getters.get_education_level;
  option_present_type.value = store.getters.get_present_type;
  option_topic_type.value = store.getters.get_topic_type;
  color_set.value = store.getters.get_color_set;
  db_test.value = store.getters.get_test_db;
  count_item();
});
const count_item = () => {
  const a = {};
  const b = {};
  const c = {};
  db_test.value.forEach((ele) => {
    a[ele.education_level] = (a[ele.education_level] || 0) + 1;
    b[ele.present_type] = (b[ele.present_type] || 0) + 1;
    c[ele.topic_type] = (c[ele.topic_type] || 0) + 1;
  });
  summary.value["level"] = a;
  summary.value["present"] = b;
  summary.value["topic"] = c;
};
</script>
<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-12 my-1 fw-bold h4">
        <i class="bi bi-buildings me-1"></i>ระดับการศึกษา
      </div>
    </div>
    <div class="row">
      <div
        class="col-2 my-1"
        v-for="(val, index) in option_education_level"
        v-bind:key="index"
      >
        <div :class="'card mb-2 text-bg-' + color_set[index]">
          <div class="card-body">
            <h5 class="card-title">{{ val.text }}</h5>
            <p class="card-text text-center fw-bold">
              {{ summary.level[val.text] }}
            </p>
          </div>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-6">
        <div class="card">
          <div class="card-body">
            <b>ลักษณะการนำเสนอ</b>
            <ol class="list-group list-group-numbered">
              <li
                class="list-group-item d-flex justify-content-between align-items-start"
                v-for="(val2, index2) in option_present_type"
                v-bind:key="index2"
              >
                <label class="ms-2 me-auto">{{ val2.text }}</label
                ><b
                  ><span
                    :class="'badge rounded-pill text-bg-' + color_set[index2]"
                    >{{ summary.present[val2.text] }}</span
                  ></b
                >
              </li>
            </ol>
          </div>
        </div>
      </div>
      <div class="col-6">
        <div class="card">
          <div class="card-body">
            <b>กลุ่มหัวข้อนำเสนอ</b>
            <ol class="list-group list-group-numbered">
              <li
                class="list-group-item d-flex justify-content-between align-items-start"
                v-for="(val3, index3) in option_topic_type"
                v-bind:key="index3"
              >
                <label class="ms-2 me-auto">{{ val3.text }}</label
                ><b
                  ><span
                    :class="'badge rounded-pill text-bg-' + color_set[index3]"
                    >{{ summary.topic[val3.text] }}</span
                  ></b
                >
              </li>
            </ol>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
